* {
  margin: 0;
  padding: 0;
  font-family: Wellfleet;
}

.About {
  background-color: rgb(255, 255, 236);
 
}

.App {
  overflow-x: hidden;
  overflow-y: auto;
}

.office {
  width: 1.3cm;
  height: auto;

}

.exp-box {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  column-gap: .35cm;
  row-gap: .35cm;

}

.exp {
  display: flex;
  box-shadow: rgba(99, 99, 99, 0.2) 0px 2px 8px 0px;
  width: 11cm;
  border: 1px solid rgb(226, 188, 255);
  align-items: flex-start;
  padding: .3cm .35cm;
  border-radius: 10px;
  background-color: rgb(237, 214, 255);
  column-gap: .2cm;
}
.year{

  font-size: 15px;
  color: rgb(78, 78, 78);
}
.type{
  margin-top: .25cm;
  font-size: 15px;
  color: rgb(78, 78, 78);
}
.place{
  color: rgb(78, 78, 78);
  font-size: 14px;
}
.role{
  font-size: 22px;
}
.cmpny-name{
  font-size: 17px;
  
}
.Home {
  display: flex;
  align-items: center;
  justify-content: center;
}

.home-buttons {
  display: flex;
  align-items: center;
  margin-top: 1cm;
  column-gap: .3cm;
}

.hb {
  padding: .28cm .7cm;
  border-radius: 10px;
  border: none;
  font-size: 18px;
  background-color: royalblue;
  color: white;
  cursor: pointer;
  transition: .2s;



}

.hb:hover {
  background-color: rgb(40, 87, 226);

}

.hbg {
  border: none;
  background-color: green;
  color: white;


}

.hbg:hover {
  background-color: rgb(2, 99, 2);

}

.home-box {
  display: flex;
  row-gap: .1cm;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 20cm;
  padding: .3cm .5cm;

}

.tagline {
  font-size: 20px;
  color: white;
  text-align: center;
  font-family: Wellfleet;

}

.name {
  font-size: 90px;
  font-family: Wellfleet;
  color: white;
  font-weight: 600;
  letter-spacing: .08cm;
}

.Home {
  position: relative;
  width: 100vw;
  height: 100vh;
}

.Home::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: -1;
  background-image: url("me.jpg");
  background-repeat: no-repeat;
  background-size: 100% auto;
  background-position: center center;
  filter: brightness(23%);
  /* Adjust the value as needed (0.7 reduces brightness) */
}

.About {

  padding: 1cm 10vw;

  /* To position the pseudo-element */
}



.about-box {
  display: flex;


  flex-direction: column;
  align-items: center;

}

.pro-dets {
  display: flex;
  flex-direction: column;
  row-gap: .4cm;
}

.heading {
  font-size: 35px;
  margin-bottom: 1.5cm;
}

.pro-para-head {
  font-size: 22px;
}

.about-dets-box {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  column-gap: .7cm;
  row-gap: .5cm;
  padding: 0cm .3cm .3cm .3cm;
}

.pro-para-box {
  display: flex;
  row-gap: .08cm;
  flex-direction: column;
}

.ppic {
  width: 7.5cm;
  height: auto;
  border-radius: 50%;
}

.exp-main {
  margin-top: 0cm;
  background-color: rgb(249, 241, 255);
  
}


@media only screen and (max-width : 918px) {

  .Home::before {
    background-size: auto 100%;
  }
}


::-webkit-scrollbar {
  width: 10px;
  height: 0px;
  cursor: pointer;
}

::-webkit-scrollbar-thumb {
  background-color: rgb(111, 111, 111);
}

::-webkit-scrollbar-track {
  background-color: transparent;
}